import * as React from 'react';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import { Button, IconButton } from '@mui/material';
import { AddBox } from '@mui/icons-material';
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';
import PropTypes from 'prop-types';
import { NumericFormat } from 'react-number-format';


//Change this list with a API
const shipperList = [
    {
        value: 'Shipper1',
        label: 'Shipper1',
    },
    {
        value: 'Shipper2',
        label: 'Shipper2',
    },
    {
        value: 'Shipper3',
        label: 'Shipper3',
    },
    {
        value: 'Shipper4',
        label: 'Shipper4',
    },
];

/* Checking if the localStorage is empty 
@orderList is the array for store the orden in localStorage
@dataOrderList is the key that save the orders in localStorage
We use first localStorage call and then empty array is case localStorage does not have orders.
*/


//Custom form general Style
const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    maxWidth: 600,
    minWidth: 245,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    display: 'flex',
    flexDirection: 'column',
    boxShadow: 24,
    zoom: '80%',
    p: 4,
    '& > :not(style)': {
        m: 1,
    }
};

// function for formating currency values
const NumericFormatCustom = React.forwardRef(function NumericFormatCustom(
    props,
    ref,
) {
    const { onChange, ...other } = props;

    return (
        <NumericFormat
            {...other}
            getInputRef={ref}
            onValueChange={(values) => {
                onChange({
                    target: {
                        name: props.name,
                        value: values.value,
                    },
                });
            }}
            thousandSeparator
            valueIsNumericString
            prefix="$"
        />
    );
});

NumericFormatCustom.propTypes = {
    name: PropTypes.string.isRequired,
    onChange: PropTypes.func.isRequired,
};

//Component Modal for creating a new order
export default function ModalNewOrder(props) {
    const [open, setOpen] = React.useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);
    const [rows, setRows] = React.useState(JSON.parse(localStorage.getItem('dataOrderList')) || []);

    //Set State for getting the input values
    const [values, setValues] = React.useState({
        shipper: '',
        orderId: '',
        country: '',
        unid: '',
        fobFormart: '',
        freightFormat: '',
        insuranceFormat: '',
        unitValue: '',
    });

    // Function for manage the data in the inputs
    const handleChange = (event) => {
        let unidValue = Number(values.fobFormart) / Number(values.unid);
        if (values.orderId === 0) {
            alert('ok')
        }

        setValues({
            ...values, unitValue: unidValue.toFixed(2),
            [event.target.name]: event.target.value,
        });
        setRows(JSON.parse(localStorage.getItem('dataOrderList')) || [])

    };

    const createNewOrder = () => {
        for(let i = 0; i < rows.length; i++)
        if(rows[i]['orderId']===values.orderId){
            alert('Ya existe una order con este Id')
            return
        }
        else if (values.orderId === '' || null) {
            alert('Debe ingresar un número de orden ID')
            return
        }
        rows.push(values);
        localStorage.setItem('dataOrderList', JSON.stringify(rows));
        setOpen(false);
        alert(`Order ${values.orderId} Creada`)
        setRows(JSON.parse(localStorage.getItem('dataOrderList')) || [])
    }

    return (
        <div>
            <IconButton aria-level={AddBox} onClick={handleOpen} >
                <AddBox></AddBox>
                <Typography>Crear Orden</Typography>
            </IconButton>
            <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style} >
                    <Typography id="modal-modal-title" variant="h5" component="h2" align="center">
                        Crear Nueva Orden
                    </Typography>
                    <TextField name="shipper"
                        id="inputShipper"
                        select
                        label="Proveedor"
                        defaultValue=""
                        helperText="Seleccione un proveedor"
                        sx={{ display: 'flex', }}
                        onChange={handleChange}
                    >
                        {shipperList.map((option) => (
                            <MenuItem key={option.value} value={option.value}>
                                {option.label}
                            </MenuItem>
                        ))}
                    </TextField>
                    <TextField name="orderId" id="inputOrderId" label="Orden ID" variant="outlined" required onChange={handleChange} />
                    <TextField name="country" id="inputCountry" label="País" variant="outlined" onChange={handleChange} />
                    <TextField name="unid" id="inputUnid" label="Kg/Unid" variant="outlined" required onChange={handleChange} />
                    <TextField
                        label="Valor FOB"
                        value={values.fobFormart}
                        onChange={handleChange}
                        name="fobFormart"
                        id="inputFob"
                        InputProps={{
                            inputComponent: NumericFormatCustom,
                        }}
                        variant="outlined"
                    />
                    <TextField
                        label="Flete"
                        value={values.freightFormat}
                        onChange={handleChange}
                        name="freightFormat"
                        id="inputFreight"
                        InputProps={{
                            inputComponent: NumericFormatCustom,
                        }}
                        variant="outlined"
                    />
                    <TextField
                        label="Seguro"
                        value={values.insuranceFormat}
                        onChange={handleChange}
                        name="insuranceFormat"
                        id="inputInsurance"
                        InputProps={{
                            inputComponent: NumericFormatCustom,
                        }}
                        variant="outlined"
                    />
                    <Button variant="contained" onClick={createNewOrder}>Crear</Button>
                </Box>
            </Modal>
        </div>
    );
}