import { Alert } from '@mui/material';

export default function AlertComponent(props) {
  const { onClose, action, children,severity } = props;

  return (
    <Alert onClose={onClose} action={action} severity={severity} variant="filled"
    sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', position:'fixed', top:'2em', left:'1em'}}>
      {children}
    </Alert>
  );
}