import React from "react";
import { LogIn } from "./Components/LogIn";
import { InfoHomeSide } from "./Components/InfoHomeSide.js";
import { styled } from "@mui/material/styles";
import Paper from "@mui/material/Paper";
import Grid from "@mui/material/Grid";
import { Box, Container } from "@mui/system";
import { Typography } from "@mui/material";

const Item = styled(Paper)(({ theme }) => ({
  // backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff', (For DarkModel no Available now)
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: "center",
  color: theme.palette.text.secondary,
  height: "100vh",
}));

export const HomeIndex = () => {
  return (
    <>
      <Grid container>
        <Grid item xs={12} md={6}>
          <Item>
            <img src="../../src/assets/Austro-icon.svg" alt="logo" />
            <LogIn />
          </Item>
        </Grid>
        <Grid item xs={12} md={6}>
          <Item sx={{ backgroundColor: "#111927", color: "white" }}>
            <InfoHomeSide></InfoHomeSide>
            <Container sx={{ display: "flex", justifyContent: "center" }}>
              <Box
                sx={{
                  position: "relative",
                  minWidth: "20em",
                  height: "30vh",
                  top: "10em",
                  color: "black",
                  backgroundImage:
                    'url("https://cdn.pixabay.com/photo/2022/08/05/00/15/logistics-7365737_960_720.jpg")',
                  backgroundSize: "cover",
                  borderRadius: "5px",
                }}
              ></Box>
            </Container>
            <Typography sx={{ marginTop: "25vh" }} variant="h4">
              Logística de Importación
            </Typography>
          </Item>
        </Grid>
      </Grid>
    </>
  );
};
