import { Container } from '@mui/system'
import React from 'react'
import ModalNewOrder from './ModalNewOrder' 


const DashMangBar = () => {


  return (

    <Container sx={
      {
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center'
      }
    } >
      <ModalNewOrder></ModalNewOrder>
    </Container>


  )
}

export default DashMangBar; 