import React from "react";
import { useState } from "react";
import { Typography } from "@mui/material";
import Box from "@mui/material/Box";
import InputAdornment from "@mui/material/InputAdornment";
import FormControl from "@mui/material/FormControl";
import TextField from "@mui/material/TextField";
import AccountCircle from "@mui/icons-material/AccountCircle";
import Button from "@mui/material/Button";
import { Container } from "@mui/system";
import { useNavigate } from "react-router-dom";
import IconButton from "@mui/material/IconButton";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";
import ComponentAlert from "../../../components/AlertComponents.js/ComponentAlert"; // Define the LogIn component

export const LogIn = () => {
  // Define state variables
  const [display, setDisplay] = useState(true); // Switch between components
  const [showPassword, setShowPassword] = React.useState(false); //Password Input show code
  const [logValues, setLogValues] = React.useState({
    //Variables for login inputs
    //Setting logIn values
    logEmail: "",
    logPassword: "",
  });
  const [signUpValues, setSignUp] = React.useState({
    //Variables for signup inputs
    //Setting sigIn <values>
    signEmail: "",
    signPassword: "",
  });
  //const for managing display errors validations
  const [emailError, setEmailError] = useState("");
  const [passwordError, setPasswordError] = useState("");

  //Define modals states
  const [emailAlreadyUsedModal, setEmailAlreadyUsedModal] = useState(false);
  const [incompleteFieldsModal, setIncompleteFieldsModal] = useState(false);

  //Define signUp modals
  const [emailDoesNotExistModal, setEmailDoesNotExistModal] = useState(false);
  const [logInErrorsModal, setLogInErrorsModal] = useState(false);
  const [userLoggedModal, setUserLoggedModal] = useState(false);

  // Define event handlers
  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };
  const handleClickShowPassword = () => setShowPassword((show) => !show);
  const displayPage = (e) => {
    //function for changing state.
    e.preventDefault();
    setDisplay(!display);
  };
  const handleChangeLogIn = (e) => {
    //Handling LogIn values onChange
    e.preventDefault();
    setLogValues({
      ...logValues,
      [e.target.name]: e.target.value,
    });
  };
  const handleChange = (event) => {
    setSignUp({
      ...signUpValues,
      [event.target.name]: event.target.value,
    });
    if (!emailRegex.test(signUpValues.signEmail)) {
      setEmailError("Ingrese un dirección de correo correcta");
    } else {
      setEmailError("");
    }
    if (!passwordRegex.test(signUpValues.signPassword)) {
      setPasswordError(
        "Debe incluir al menos 8 caracteres, mayúscula, minúsculas y números "
      );
    } else {
      setPasswordError("");
    }
  };
  // Define validation logic
  const emailRegex = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i;
  const passwordRegex =
    /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[!@#$%^&*()_+=[\]{}|\\:;'<>,.?/~`])(?=.*[^\da-zA-Z]).{8,}$/;
  let valueEmail = logValues.logEmail;
  let valuePassword = logValues.logPassword;
  let dataUserStored = JSON.parse(localStorage.getItem("dataUserList")) || [];

  const navigate = useNavigate();
  const confirmLogIn = () => {
    let filterList = dataUserStored.filter(
      (user) => user.signEmail === valueEmail
    );
    if (valueEmail === "" || valuePassword === "") {
      setLogInErrorsModal(true);
    } else {
      if (filterList.length > 0) {
        if (
          filterList[0]["signEmail"] === valueEmail &&
          filterList[0]["signPassword"] === valuePassword
        ) {
          setUserLoggedModal(true);
          setTimeout(() => {
            navigate("/HomeDash");
          }, 2000);
        } else if (
          filterList[0]["signEmail"] !== valueEmail &&
          filterList[0]["signPassword"] !== valuePassword
        ) {
          return setLogInErrorsModal(true);
        }
      } else {
        setEmailDoesNotExistModal(true);
      }

    }

  };

  // Define logic for storing new user
  const userList = JSON.parse(localStorage.getItem("dataUserList")) || [];
  const newUser = (e) => {

    //Verificar que solo tome el primero elemento y no la lista iterada de elementos. 
    e.preventDefault();
    const isUserFiltered = dataUserStored.some(
      (user) => user.signEmail === signUpValues.signEmail 
    ); 
    if (isUserFiltered) {
      setEmailAlreadyUsedModal(true);
      return
    } else if (
      !emailRegex.test(signUpValues.signEmail) || !passwordRegex.test(signUpValues.signPassword)) {
      return setIncompleteFieldsModal(true);
    } else {
      userList.push(signUpValues);
      localStorage.setItem("dataUserList", JSON.stringify(userList));
      alert("Welcome user saved");
      navigate("/HomeDash");
      setSignUp("");
    }
  
};

return (
  <>
    {/* Open the {} for introduce the two page */}
    {display ? (
      <Container sx={{ m: 1, marginTop: "20vh" }} variant="filled">
        <Typography variant="h4" sx={{ paddingBottom: "1em" }}>
          Ingresar
        </Typography>
        <Typography variant="p">
          ¿No tienes una cuenta aun? {/* Call the function for change */}
          <a onClick={displayPage} href="./">
            Registrate
          </a>
        </Typography>
        <Box sx={{ "& > :ot(style)": { m: 4 } }}>
          <FormControl
            id="form"
            variant="standard"
            sx={{ paddingTop: "2em" }}
          >
            <TextField
              sx={{ paddingBottom: "2em" }}
              id="input-with-icon"
              label="Correo"
              name="logEmail"
              onChange={handleChangeLogIn}
              placeholder="Ingrese correo registrado"
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <AccountCircle />
                  </InputAdornment>
                ),
              }}
              variant="standard"
            />
            {emailDoesNotExistModal ? (
              <ComponentAlert // Email does not exist
                sx={{ position: "relative", zIndex: "1" }}
                severity="error"
                onClose={() => {
                  setEmailDoesNotExistModal(false);
                }}
              >
                ¡El correo ingresado no encontrado!
              </ComponentAlert>
            ) : (
              ""
            )}
            {logInErrorsModal ? (
              <ComponentAlert //Password Error
                sx={{ position: "relative", zIndex: "1" }}
                severity="warning"
                onClose={() => {
                  setLogInErrorsModal(false);
                }}
              >
                ¡Compruebe las credenciales!
              </ComponentAlert>
            ) : (
              ""
            )}
            {userLoggedModal ? (
              <ComponentAlert //LogIn Success
                sx={{ position: "relative", zIndex: "1" }}
                severity="success"
                onClose={() => {
                  setUserLoggedModal(false);
                }}
              >
                ¡Bienvenida/o!
              </ComponentAlert>
            ) : (
              ""
            )}
            <TextField
              sx={{ paddingBottom: "1em" }}
              // id="input-with-icon-textfield"
              label="Contraseña"
              name="logPassword"
              onChange={handleChangeLogIn}
              placeholder="*****"
              type={showPassword ? "text" : "password"}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <IconButton
                      aria-label="toggle password visibility"
                      onClick={handleClickShowPassword}
                      onMouseDown={handleMouseDownPassword}
                    >
                      {showPassword ? <VisibilityOff /> : <Visibility />}
                    </IconButton>
                  </InputAdornment>
                ),
              }}
              variant="standard"
            />
            <Button variant="contained" onClick={confirmLogIn}>
              Ingresar
            </Button>
          </FormControl>
        </Box>
      </Container>
    ) : (
      <Container sx={{ m: 1, marginTop: "20vh" }} variant="filled">
        {emailAlreadyUsedModal ? (
          <ComponentAlert // Error email taken
            sx={{ position: "relative", zIndex: "1" }}
            severity="error"
            onClose={() => {
              setEmailAlreadyUsedModal(false);
            }}
          >
            ¡El correo ingresado ya ha sido registrado!
          </ComponentAlert>
        ) : (
          ""
        )}
        {incompleteFieldsModal ? (
          <ComponentAlert // Error input empty
            sx={{ position: "relative", zIndex: "1" }}
            severity="warning"
            onClose={() => {
              setIncompleteFieldsModal(false);
            }}
          >
            ¡Complete todos los campos!
          </ComponentAlert>
        ) : (
          ""
        )}
        <Typography variant="h4" sx={{ paddingBottom: "1em" }}>
          Registarse
        </Typography>
        <Typography variant="p">
          ¿Ya tienes una cuenta?
          <a onClick={displayPage} href="./">
            Ingresar
          </a>
        </Typography>
        {/* Call the function for change */}
        <Box sx={{ "& > :ot(style)": { m: 4 } }}>
          <FormControl
            id="form"
            variant="standard"
            sx={{ paddingTop: "2em" }}
          >
            <TextField
              name="signEmail"
              sx={{ paddingBottom: "1em" }}
              id="input-with-icon-textfield"
              label="Correo"
              onChange={handleChange}
              placeholder="Ingrese un nuevo correo"
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <AccountCircle />
                  </InputAdornment>
                ),
              }}
              variant="standard"
            />
            <Typography
              variant="caption"
              sx={{ margin: "1px", padding: "1px", color: "#ff9800" }}
            >
              {emailError}
            </Typography>
            <TextField
              name="signPassword"
              sx={{ paddingBottom: "1em" }}
              id="input-with-icon-textfield"
              label="Contraseña"
              onChange={handleChange}
              placeholder="******"
              type={showPassword ? "text" : "password"}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <IconButton
                      aria-label="toggle password visibility"
                      onClick={handleClickShowPassword}
                      onMouseDown={handleMouseDownPassword}
                    >
                      {showPassword ? <VisibilityOff /> : <Visibility />}
                    </IconButton>
                  </InputAdornment>
                ),
              }}
              variant="standard"
            />
            <Typography
              variant="caption"
              sx={{ color: "#ff9800", paddingBottom: "2em" }}
            >
              {passwordError}
            </Typography>
            <Button variant="contained" onClick={newUser} type="reset">
              Registrarse
            </Button>
          </FormControl>
        </Box>
      </Container>
    )}
    ;{/* Close ternary */}
  </>
);
};
